<template>
    <div>
        <div class="kd-page-title">
            选择开具事由
        </div>
        <div class="kd-page-content-wrapper">
            <el-form
                class="uploadLabel search-form"
                label-width="15vw"
            >
                <el-form-item label="开具事由：">
                    <el-radio-group v-model="searchType">
                        <el-radio
                            class="uploadChoose"
                            v-for="(item,index) in searchTypes"
                            :key="index"
                            :label="item.type"
                            border
                        >
                            {{ item.value }}
                        </el-radio>
                    </el-radio-group>
                </el-form-item>
            </el-form>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="nextStep"
                :but-disable="!searchType"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { saveFaceReco } from '../../../../../service/human/prove';
import { addPrefix } from '@/utils/tools';

export default {
    name: 'TemporaryCardYinchuanReasonsType',
    computed: {
        ...mapState('common', {
            identifyCard: 'identifyCard'
        })
    },
    data () {
        return {
            searchType: '',
            searchTypes: [
                {
                    type: '1',
                    value: '购票'
                },
                {
                    type: '2',
                    value: '住宿'
                }
            ]
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage', 'setPrintImgUrls']),
        ...mapMutations('temporary', ['setPdfUrl']),
        async nextStep () { // 下一步
            const res = await saveFaceReco({
                idNum: this.identifyCard.certNumber,
                useType: this.searchType,
                businessType: 'lssfzzm' // 业务类型 lssfzzm 临时身份证证明
            });
            if (res && res.code === '0') {
                this.setPrintImgUrls(addPrefix(res.result.pdfImage));
                this.setPdfUrl(res.result.pdfUrl);
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({
                    message: res.message,
                    enableBack: true
                });
            }
        },
        preStep () { // 上一步
            this.changeStep('up');
        }
    }
};
</script>

<style lang="scss" scoped>
    .search-form {
        margin: 7.5vw 2vw auto;
    }

    .uploadLabel .uploadChoose {
        width: 18.8vw !important;
    }

    .el-radio.is-bordered + .el-radio.is-bordered {
        margin-left: 1.8vw;
    }
</style>
