<template>
    <div>
        <identify-card-page
            v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"
            :disable-read-card="true"
        />

		<process
				:type="2"
				v-if="currentStep === steps.findIndex(step=>step.key === 'getIdCardPhoto')"
				:handle-business="getIdCardPhoto"
				wait-text="正在处理中，请稍等"
		/>
		<face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
		<contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>

        <reasons-type v-if="currentStep === steps.findIndex(step=>step.key === 'reasonsType')"/>
        <person-info v-if="currentStep === steps.findIndex(step=>step.key === 'personInfo')"/>
        <process
            :type="1"
            v-if="currentStep === steps.findIndex(step=>step.key === 'printProcess')"
            :handle-business="handlePrint"
            wait-text="正在打印中，请稍等"
        />
        <complete
            v-if="currentStep === steps.findIndex(step=>step.key === 'complete')"
            page-title="办理完成"
            :data="temporaryCompleteRes"
        />
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import personInfo from './components/person-info';
import reasonsType from './components/reasons-type';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import process from '@/components/process';
import complete from '@/components/complete';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import { getIdCardPhotoServer, proveComplete } from '@/service/human/prove';
import { toPrint } from '@/service/common';
import { getPrintPrefix } from '@/utils';
import { ID_NUMBER_TEMP_YINCHUAN } from '@/constant/stepsOption';

export default {
    name: 'TemporaryCardYinchuanIndex', // 临时身份证证明
    components: {
        personInfo,
        identifyCardPage,
        process,
        complete,
        reasonsType,
        faceYitu,
        contrast
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            identifyCard: 'identifyCard'
        }),
        ...mapState('temporary', {
            pdfUrl: 'pdfUrl'
        })
    },
    data () {
        return {
            temporaryCompleteRes: {}
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage', 'setSteps', 'setIdentifyCardPhoto']),
        async getIdCardPhoto () {
            const res = await getIdCardPhotoServer({
                idNum: this.identifyCard.certNumber,
                businessType: 'lssfzzm'
            });
            if (res && res.code === '0') {
                if (this.identifyCard.partyName !== res.result.xm) {
                    this.changeTipPage({ message: '未查到该人员信息，请确认重试', enableBack: true });
                } else {
                    this.setIdentifyCardPhoto(res.result.xp.replace(/ /g, '+'));
                    this.changeStep('down');
                }
            } else if (res) {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async handlePrint () {
            // 打印证明
            const res = await toPrint({
                path: getPrintPrefix(this.pdfUrl)
            });
            if (res && res.code === 1) {
                this.complete();
            } else {
                this.changeTipPage({ message: '打印机出故障啦，请稍后重试' });
            }
        },
        async complete () {
            // 打印完成调用 办理完成结果
            const res = await proveComplete({});
            if (res && res.code === '0') {
                this.temporaryCompleteRes = res.result;
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        }
    },
    mounted () {
        this.setSteps(ID_NUMBER_TEMP_YINCHUAN);
    }
};

</script>
